"use client";
import React, { useRef, useState } from "react";
import { Eye, Calendar, Play } from "lucide-react";
import { useRouter } from "next/navigation";

interface HighlightCardProps {
    video: {
        title: string;
        summary?: string;
        category?: { name: string };
        views_count?: number;
        published_at?: string;
        video_url: string;
        slug: string;
    };
    className?: string;
    hidePlayButton?: boolean;
    largeText?: boolean;
}

const HighlightCard: React.FC<HighlightCardProps> = ({
                                                         video,
                                                         className = "",
                                                         largeText = false,
                                                         hidePlayButton = false,
                                                     }) => {
    const router = useRouter();
    const videoRef = useRef<HTMLVideoElement>(null);
    const [isHovered, setIsHovered] = useState(false);

    const formatViews = (num?: number) => {
        if (!num) return "0";
        if (num >= 1000000) return (num / 1000000).toFixed(1) + "M";
        if (num >= 1000) return (num / 1000).toFixed(1) + "k";
        return num.toString();
    };

    const formatDate = (dateString?: string) => {
        if (!dateString) return "";
        const date = new Date(dateString);
        return date.toLocaleDateString("en-US", { month: "short", day: "numeric" });
    };

    return (
        <div
            className={`group relative overflow-hidden rounded-2xl cursor-pointer bg-slate-900 ${className}`}
            onMouseEnter={() => {
                setIsHovered(true);
                videoRef.current?.play();
            }}
            onMouseLeave={() => {
                setIsHovered(false);
                videoRef.current?.pause();
                videoRef.current!.currentTime = 0;
            }}
            onClick={() => router.push(`/highlight/${video.slug}`)}
        >
            {/* Video */}
            <video
                ref={videoRef}
                src={`https://pub-64a1f52f8ce34898ad37705d90a1d23b.r2.dev/${video.video_url}`}
                className="absolute inset-0 w-full h-full object-cover transition-transform duration-700 group-hover:scale-105"
                muted
                loop
                playsInline
            />

            {/* Dark Gradient Overlay */}
            <div className="absolute inset-0 bg-gradient-to-t from-black/95 via-black/40 to-transparent"></div>
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/10 transition-colors duration-300"></div>

            {/* Play Icon Overlay */}
            {!hidePlayButton && (
                <div
                    className={`absolute inset-0 flex items-center justify-center pointer-events-none transition-opacity duration-300 ${
                        isHovered ? "opacity-0" : "opacity-100"
                    }`}
                >
                    <div className="bg-white/20 backdrop-blur-md rounded-full p-3 ">
                        <Play className="w-8 h-8 text-white" />
                    </div>
                </div>
            )}

            {/* Content Container */}
            <div className="absolute inset-0 p-5 md:p-6 flex flex-col justify-between z-10">
                {/* Top Header Row */}
                <div className="flex justify-between items-start">
                    {video.category?.name && (
                        <span className="bg-white/20 backdrop-blur-md text-white text-[10px] uppercase font-bold px-2 py-1 rounded tracking-wider border border-white/10 shadow-sm">
              {video.category.name}
            </span>
                    )}
                </div>

                {/* Bottom Text Content */}
                <div>
                    <h3
                        className={`font-bold text-white leading-tight mb-2 ${
                            largeText ? "text-2xl md:text-3xl" : "text-lg md:text-xl"
                        } group-hover:text-slate-200 transition-colors`}
                    >
                        {video.title}
                    </h3>

                    {video.summary && largeText && (
                        <p className="text-slate-300 text-sm mb-4 line-clamp-2 hidden sm:block">{video.summary}</p>
                    )}

                    <div className="flex items-center gap-3 text-slate-300 text-xs font-medium">
                        <span className="flex items-center gap-1.5" title={`${video.views_count} views`}>
                            <Eye className="w-3 h-3" /> {formatViews(video.views_count)}
                        </span>

                        <span className="w-1 h-1 rounded-full bg-slate-500"></span>

                        <span className="flex items-center gap-1.5">
                            <Calendar className="w-3 h-3" /> {formatDate(video.published_at)}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HighlightCard;
