"use client";
import Link from "next/link";
import HighlightCard from "@/app/src/components/highlightCard";
import {useHighlight} from "@/app/src/hook/useHighlight";

export default function HighlightHero() {
    const {data:highlights, isLoading, isError} = useHighlight();
    if(isLoading) return null;
    if(isError) return <div>Error</div>;
    const highlightArray = Array.isArray(highlights) ? highlights : (highlights?.data || []);
    if(!isLoading) console.log(highlightArray);
    return (
        <section className="w-full my-8">
            <div className="flex justify-between items-center mb-4">
                <div className="bg-[#4c3b71] text-white px-4 py-1.5 rounded-md text-sm font-bold flex items-center gap-2 h-10">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    HIGHLIGHTS
                </div>

                <Link href="/highlight" className="text-[#4c3b71] font-bold text-sm hover:underline flex items-center gap-1">
                    More Highlights
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                </Link>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 auto-rows-[200px] gap-4">
                <HighlightCard
                    video={highlightArray[0]}
                    largeText={true}
                    className="col-span-1 sm:col-span-2 lg:col-span-2 row-span-2 h-[350px] sm:h-auto"
                />
                <HighlightCard
                    video={highlightArray[1]}
                    className="col-span-1 lg:col-span-1 lg:row-span-2 h-[200px] lg:h-auto"
                />
                <HighlightCard
                    video={highlightArray[2]}
                    className="col-span-1 row-span-1"
                />
                <HighlightCard
                    video={highlightArray[0]}
                    className="col-span-1 row-span-1"
                />
                <HighlightCard
                    video={highlightArray[0]}
                    className="col-span-1 sm:col-span-1 lg:col-span-2 row-span-1"
                />
                <HighlightCard
                    video={highlightArray[0]}
                    className="col-span-1 sm:col-span-2 lg:col-span-2 row-span-1"
                />
            </div>

        </section>
    );
}